    },
    // 删除
    deleteAll() {
      this.showModal = true
      this.isDelAll = true
    },
    // 全部删除和单个删除
    confirm() {
      setTimeout(() => {
        if (this.isDelAll) {
          uni.removeStorageSync('searchHistory')
          this.searchHistory = []
          return
        } else {
          this.searchHistory.splice(this.deleteIndex, 1)
          uni.setStorageSync('searchHistory', JSON.stringify(this.searchHistory))
        }
        this.$toast('删除成功')
