    commonSearchHandler(val) {
      this.keywords = val
      if (val.length <= 0) {
        return this.$toast('请输入关键词')
      }
      let index = this.searchHistory.indexOf(val)
      if (index === -1) {
        // 去重
        this.searchHistory.unshift(val)
      } else {
        // 首位添加
        if (index !== 0) {
          console.log('执行')
          this.searchHistory.unshift(this.searchHistory.splice(index, 1)[0])
          this.searchHistoryIndex = 0
        }
      }
      // 移除最后一条 只需要10条数据
      if (this.searchHistory.length > 10) {
        this.searchHistory.splice(this.searchHistory.length - 1, 1)
      }
      console.log('this.searchHistory', this.searchHistory)
      uni.setStorageSync('searchHistory', JSON.stringify(this.searchHistory))
    },
