<template>
  <div>
  <deng-long1></deng-long1>
  <div class="picture">
    <div class="section">
      <div v-for="(item,index) in list" :key="index" @click="jumpDetail(item)"><img :src="item.img" alt=""></div>
    </div>
  </div>
  </div>
</template>

<script>
import { lists } from '@/utils/Picture.js'
console.log(lists)
export default {
  data() {
    return {
      list:[]
    }
  },
  methods: {
    jumpDetail(item) {
      this.$router.push({
        name: 'Detail',
        query: {
          item
        }
      })
    },
  },
  mounted() {
    this.list = lists
  }
}
</script>

<style scoped>
.picture {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #000;
  perspective: 1000px;
}
.section {
  position: relative;
  width: 100px;
  height: 160px;
  cursor: pointer;
  transform-style: preserve-3d;
  animation: rotate 15s linear infinite;
}
.section:active {
  /* 鼠标移上动画暂停 */
  animation-play-state: paused;
}
.section div {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  /* 设置元素的倒影效果，below是倒影效果在元素下方，15px是元素和倒影的距离，后面的属性是设置倒影渐变 */
  -webkit-box-reflect: below 15px -webkit-linear-gradient(transparent 50%, rgba(255, 255, 255, 0.3));
}
.section div img {
  width: 100%;
  height: 100%;
}
.section div:nth-child(1) {
  transform: rotateY(0deg) translateZ(200px);
}
.section div:nth-child(2) {
  transform: rotateY(45deg) translateZ(200px);
}
.section div:nth-child(3) {
  transform: rotateY(90deg) translateZ(200px);
}
.section div:nth-child(4) {
  transform: rotateY(135deg) translateZ(200px);
}
.section div:nth-child(5) {
  transform: rotateY(180deg) translateZ(200px);
}
.section div:nth-child(6) {
  transform: rotateY(225deg) translateZ(200px);
}
.section div:nth-child(7) {
  transform: rotateY(270deg) translateZ(200px);
}
.section div:nth-child(8) {
  transform: rotateY(315deg) translateZ(200px);
}

/* 定义旋转动画 */
@keyframes rotate {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
</style>
